import React, { useEffect, useRef, useState } from 'react'

const Dropdown = ({ label, options, selected, onSelectedChange, removeHeading }) => {
    const [open, setOpen] = useState(false)
    const ref = useRef()

    useEffect(() => {
        const onBodyClick = (event) => {
            if (ref.current && ref.current.contains(event.target)) {
               return;
            }
        
            setOpen(false);
        }
        
        document.body.addEventListener('click', onBodyClick);
        
        return () => {
            document.body.removeEventListener('click', onBodyClick);
        }

    }, [])

    const renderedOptions = options.map((option) => {
        if (option.value === selected.value) {
            return null
        }

        return (
            <div className="item" key={option.value} onClick={() => onSelectedChange(option)}>
                {option.label}
            </div>
        )
    })

    return (
        <div ref={ref} className="ui form">
            <div className="field">
                <label className="label">{label}</label>
                <div onClick={() => setOpen(!open)} className={`ui selection dropdown ${open ? 'visible active' : ''}`}>
                    <i className="dropdown icon"></i>
                    <div className="text">{selected.label}</div>
                    <div className={`menu ${open ? 'visible transition' : ''}`}>
                        {renderedOptions}
                    </div>
                </div>
            </div>

           {removeHeading ? '' : <h3 className={`ui header ${selected.value}`}>This text is {selected.value}!</h3>}

        </div>
    )
}

export default Dropdown